@media only screen and (max-width: 650px) {
  * {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent; /* For some Androids */
  }
  .top-animate {
    background: #000 !important;
    top: 13px !important;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .mid-animate {
    opacity: 0;
  }
  .bottom-animate {
    background: #000 !important;
    top: 13px !important;
    -webkit-transform: rotate(-225deg);
    transform: rotate(-225deg);
  }
  .top-menu {
    top: 5px;
    width: 25px;
    height: 2px;
    border-radius: 10px;
    background-color: #f9a530;
  }
  .mid-menu {
    top: 13px;
    width: 25px;
    height: 2px;
    border-radius: 10px;
    background-color: #f9a530;
  }
  .bottom-menu {
    top: 21px;
    width: 25px;
    height: 2px;
    border-radius: 10px;
    background-color: #f9a530;
  }
  .menui {
    background: #000;
    transition: 0.6s ease;
    transition-timing-function: cubic-bezier(0.75, 0, 0.29, 1.01);
    margin-top: 10px;
    position: absolute;
  }
  .icon {
    z-index: 999;
    position: absolute;
    display: block;
    padding: 9px;
    height: 38px;
    width: 38px;
    top: 25px;
    margin: 0px;
    right: 1%;
    float: right;
  }
  .mobilenav {
    font-family: inherit;
    top: 0;
    left: 0;
    z-index: 999;
    display: none;
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .mobilenav li {
    list-style-type: none;
    text-align: center;
    padding: 10px;
  }
  .mobilenav li a {
    font-size: 2400%;
    font-size: 150%;
    color: #000;
    text-decoration: none;
    font-weight: 300;
    width: 100%;
  }
  .mobilenav li:first-child {
    margin-top: 60px;
  }
}
